                                                            <feFuncR type="linear" slope="3" intercept="0"></feFuncR>
                                                            <feFuncG type="linear" slope="1.5" intercept="0.25"></feFuncG>
                                                            <feFuncB type="linear" slope="1.5" intercept="0.5"></feFuncB>
