                                                            <p class=".body-1">2. <code>type="identity"</code></p>
                                                            <p class=".body-1">3. <code>type="linear"</code><code>slop * c + intercept</code></p>
                                                            <p class=".body-1">4. <code>type="table"</code><code>tableValues</code></p>
<pre>           
    | |      |  |
    | 0 ~ 0.25  |      | 0 ~ p1        |
    | 0.25 ~ 0.5|      | p1 ~ p2       |
    | 0.5 ~ 0.75|      | p2 ~ p3       |
    | 0.75 ~ 1  |      | p3 ~ p4       |
</pre>
                                                            <p class=".body-1">5. <code>type="gamma"</code><code>amplitude * c<sup>exponent</sup> + offset</code></p>

                                                        </v-card-text>
