                        <feMerge>
                            <feMergeNode />
                            <feMergeNode in="SourceGraphic" />
                        </feMerge>
                    </filter>
                </defs>
                <circle cx="100" cy="100" r="50" fill="green" filter="url(#fds2)"/>
            </svg>

            <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
                <defs>
                    <filter id="fds3" filterUnits="userSpaceOnUse">
                        <feGaussianBlur in="SourceGraphic" stdDeviation="6" result="fb"/>
                        <feOffset in="fb" dx="6" dy="6" result="fo"/>
                        <feMerge>
                            <feMergeNode in="fo" />
                            <feMergeNode in="SourceGraphic" />
                        </feMerge>
                    </filter>
                </defs>
                <circle cx="100" cy="100" r="50" fill="green" filter="url(#fds3)"/>
            </svg>
            `,
            morphologyhtml: `
            <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
                <defs>
                    <filter id="fmp1" filterUnits="userSpaceOnUse">
                        <feMorphology operator="erode" radius="1 2"/>
                    </filter>
                </defs>
                <image 
                    xlink:href="../../assets/submenu.jpeg" 
                    height="200" width="200" 
                    filter="url(#fmp1)"/>
                <text x="100" y="100" text-anchor="middle" fill="white">erode</text>
            </svg>

            <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
                <defs>
                    <filter id="fmp2" filterUnits="userSpaceOnUse">
                        <feMorphology operator="dilate" radius="2"/>
                    </filter>
                </defs>
                <image 
                    xlink:href="../../assets/submenu.jpeg" 
                    height="200" width="200" 
                    filter="url(#fmp2)"/>
                <text x="100" y="100" text-anchor="middle" fill="white">dilate</text>
            </svg>
            `,
            tilehtml: `
            <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
                <defs>
                    <filter id="ft" x="0" y="0" width="100%" height="100%">
                        <feTile 
                            in="SourceGraphic" 
                            x="0" y="0"
                            width="50" 
                            height="50" />
                        <feTile/>
                    </filter>
                </defs>

                <image 
                    xlink:href="../../assets/submenu.jpeg" 
                    x="0" 
                    y="0" 
                    style="filter:url(#ft);"
                />
            </svg>
            `,
            turbulencehtml: `
            <svg width="600" height="200" xmlns="http://www.w3.org/2000/svg">
                <defs>
                    <filter id="ftb" x="0" y="0" width="100%" height="100%">
                        <feTurbulence 
                            type="fractalNoise" 
                            baseFrequency="0.04 0.03"
                            numOctaves="5"
                            seed="5"
                            stitchTiles="stitch"
                            result="ftb"/>
                        <feDisplacementMap 
                            in2="ftb" 
                            in="SourceGraphic"
                            scale="50" 
                            xChannelSelector="B" 
                            yChannelSelector="B"/>
                    </filter>
                </defs>
                <image 
                    xlink:href="../../assets/submenu.jpeg" 
                    x="0" 
                    y="0" 
                    style="filter:url(#ftb);"
                />
                <image 
                    xlink:href="../../assets/submenu.jpeg" 
                    x="100" 
                    y="0" 
                    style="filter:url(#ftb);"
                />
            </svg>
            `
        };
    },
    mounted() {
        this.handleWave();
    },
    methods: {
        handleWave() {
            // 
            const feImage = document.querySelector("#fdm feImage");
            const ripples = document.querySelector("#ripples");
            const xlink = "http://www.w3.org/1999/xlink";

            let displacement = 0;
            let speed = 0.2;
            
            // svgdatauri
            function generateGradient() {
                /*
                <svg version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' width="300" height="300">
                    <defs>
                        <radialGradient id="rg" r=".9"> 
                            <stop offset='0%' stop-color='#f00'></stop>
                            <stop offset='10% 'stop-color='#000'></stop>
                            <stop offset='20%' stop-color='#f00'></stop>
                            <stop offset='30%' stop-color='#000'></stop>
                            <stop offset='40%' stop-color='#f00'></stop>
                            <stop offset='50%' stop-color='#000'></stop>
                            <stop offset='60%' stop-color='#f00'></stop>
                            <stop offset='70%' stop-color='#000'></stop>
                            <stop offset='80% 'stop-color='#f00'></stop>
                            <stop offset='90%' stop-color='#f00'></stop> 
                            <stop offset='100%' stop-color='#f00'></stop>
                        </radialGradient> 
                    <defs>
                    <rect width="300" height="300" fill="url(#rg)"></rect>
                </svg>
                */
                // 
                // '&' -> '%26'
                // '|' -> '%7C'
                // '[' -> '%5B'
                // ']' -> '%5D'
                // '^' -> '%5E'
                // '`' -> '%60'
                // ';' -> '%3B'
                // '?' -> '%3F'
                // ':' -> '%3A'
                // '@' -> '%40'
                // '=' -> '%3D'
                let xlinkHref = "data:image/svg+xml;utf8,%3Csvg version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' width='300' height='300'%3E%3Cdefs%3E%3CradialGradient id='rg' r='.9'%3E";
                for (var i = 0; i < 11; i++) {
                    xlinkHref += `%3Cstop 
                                offset='${(i - 2) * 10 + displacement}%25' 
                                stop%2Dcolor='%23${i % 2 == 0 ? "3f0" : "000"}'%3E%3C/stop%3E`;
                }
                xlinkHref +=
                    "%3C/radialGradient%3E%3C/defs%3E%3Crect width='300' height='300' fill='url(%23rg)'%3E%3C/rect%3E%3C/svg%3E";
                return xlinkHref;
            }
            function AnimateOffset() {
                let xlinkHref = generateGradient();
                feImage.setAttributeNS(xlink, "href", xlinkHref);
                ripples.setAttributeNS(xlink, "href", xlinkHref);
                if (displacement <= 20) {
                    displacement += speed;
                } else {
                    displacement = 0;
                }
                window.requestAnimationFrame(AnimateOffset);
            }
            AnimateOffset();
            window.requestAnimationFrame(AnimateOffset);
        }
    },
};
</script>

